.form {
  max-width: 230px;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}

.input {
  padding: 5px;
  margin-bottom: 20px;
}

.checkbox {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}

.checkbox input {
  margin-right: 10px;
  cursor: pointer;
}

.submit {
  cursor: pointer;
}
